import React, { useState } from "react";
import PropTypes from "prop-types";
import { useDispatch, useSelector } from "react-redux";
import { logoutUser } from "../../actions/authActions";
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import { AccountCircle } from "@mui/icons-material";
import logo from '../../assets/logo.png'; // ודא שהנתיב ללוגו נכון
import { List, ListItem, ListItemText } from "@mui/material";

const Navbar = () => {
    const dispatch = useDispatch();
    const auth = useSelector(state => state.auth);
    const { user } = auth;

    const [anchorEl, setAnchorEl] = useState(null);
    const [drawerOpen, setDrawerOpen] = useState(false);
    const open = Boolean(anchorEl);

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const onLogoutClick = e => {
        e.preventDefault();
        dispatch(logoutUser());
        handleClose();
    };

    const toggleDrawer = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setDrawerOpen(open);
    };

    return (
        <div className="w-full">
            <AppBar position="static" className="bg-blue-500">
                <Toolbar className="flex justify-between">
                    <div className="flex items-center">
                        <IconButton
                            size="large"
                            edge="start"
                            color="inherit"
                            aria-label="menu"
                            className="mr-2"
                            onClick={toggleDrawer(true)}
                        >
                            <MenuIcon />
                        </IconButton>
                        <img src={logo} alt="logo" className="h-10 mr-2" />
                        <Typography variant="h6" component="div" className="text-white">
                            שם החברה
                        </Typography>
                    </div>
                    <div className="flex items-center">
                        {auth.isAuthenticated ? (
                            <>
                                <IconButton
                                    size="large"
                                    aria-label="account of current user"
                                    aria-controls="menu-appbar"
                                    aria-haspopup="true"
                                    onClick={handleMenu}
                                    color="inherit"
                                >
                                    <AccountCircle />
                                </IconButton>
                                <Menu
                                    id="menu-appbar"
                                    anchorEl={anchorEl}
                                    anchorOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    keepMounted
                                    transformOrigin={{
                                        vertical: 'top',
                                        horizontal: 'right',
                                    }}
                                    open={open}
                                    onClose={handleClose}
                                >
                                    <MenuItem onClick={onLogoutClick}>התנתק ({user.name})</MenuItem>
                                </Menu>
                            </>
                        ) : (
                            <Button color="inherit" className="text-white">Login</Button>
                        )}
                    </div>
                </Toolbar>
            </AppBar>

            {/* Drawer for mobile view */}
            <div className={`fixed inset-0 z-50 bg-black bg-opacity-50 transition-opacity ${drawerOpen ? 'opacity-100' : 'opacity-0 pointer-events-none'}`} onClick={toggleDrawer(false)}>
                <div className={`fixed top-0 left-0 h-full w-64 bg-white shadow-lg transform transition-transform ${drawerOpen ? 'translate-x-0' : '-translate-x-full'}`}>
                    <List>
                        {auth.isAuthenticated ? (
                            <ListItem button onClick={onLogoutClick}>
                                <ListItemText primary={`התנתק (${user.name})`} />
                            </ListItem>
                        ) : (
                            <ListItem button>
                                <ListItemText primary="Login" />
                            </ListItem>
                        )}
                    </List>
                </div>
            </div>
        </div>
    );
};

Navbar.propTypes = {
    logoutUser: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired
};

export default Navbar;
